import React, { Component } from 'react'
import './clients.css'
import '../../node_modules/antd/dist/antd.css';
import {Row,Col,Typography,Button} from  'antd'
import {RightCircleFilled} from '@ant-design/icons';
const {Title}=Typography;
class Feedback extends Component
{
    render()
    {
        return(
            <div >
            <div className='my-5 d-flex flex-column align-items-center'>
                <Row className='my-3  d-flex justify-content-center' style={{width:'97%'}}>
                    <Col span={24}><Title level={3} className='text-center my-0' style={{color:'rgb(208,53,57)'}}>Services Testimonials</Title></Col>
                    <Col span={24}><Title level={1} className='text-center my-0 mb-4'>What Clients Say About Our Company</Title></Col>
                      <div style={{width:"150px",borderBottom:'2px solid rgb(208,53,57)'}}></div>
                </Row>
                <div className="my-3" style={{width:'97%'}}>
                      <Title level={5} className='text-center'>Our Clients Speaks For Themselves On Various Platforms</Title>
                      <div className='d-flex justify-content-center flex-wrap'>
                          <img src={process.env.PUBLIC_URL+'/img/f1.jpg'} className='my-3 mx-3 img-fluid' style={{width:'110px',height:'auto'}}/>
                          <img src={process.env.PUBLIC_URL+'/img/f2.jpg'} className='my-3 mx-3 img-fluid'/>
                          <img src={process.env.PUBLIC_URL+'/img/f3.jpg'} className='my-3 mx-3 img-fluid' style={{width:'110px',height:'auto'}}/>
                      </div>
                </div>
            </div>
            </div>
           
        )
    }
    
}
export default Feedback;