import React, { Component } from 'react'
import './title.css'
import '../../node_modules/antd/dist/antd.css';
import {Row,Col,Typography,Button} from  'antd'
import {ArrowRightOutlined} from '@ant-design/icons';
const {Title}=Typography;
class TitleTop extends Component
{
    render()
    {
        return(
            
            <div style={{backgroundImage:`linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.7)),url(${process.env.PUBLIC_URL+'/img/back.jpg'})`}} className='title d-flex flex-column align-items-center justify-content-center'>
            <div className='my-5' style={{width:'93%'}}>
                    <Title level={3}  style={{color:"rgb(208,53,57)"}} className='my-0 text-center'>{this.props.t1}</Title>
                    <Title level={1} className='text-white my-0 mb-4 text-center'>{this.props.t2}</Title>
                    <div className='d-flex justify-content-center'>
                    <Button type='primary' size='large' shape='round' style={{backgroundColor:'rgb(208,53,57)',borderColor:"rgb(208,53,57)"}}>Let's Get started</Button>
                </div>
            </div>
        </div>
        )
    }
}
export default TitleTop;