import React, { Component } from 'react'
import './counter.css'
import {ProjectOutlined,GlobalOutlined,HeartOutlined,GiftOutlined} from '@ant-design/icons';
export default class Counter extends Component {
    render() {
        return (
            <div className='analysis_counter py-5'  >
                         <div>
                         <div><ProjectOutlined /><span>30+</span><span>Project Finished</span></div>
                         <div><HeartOutlined /><span>16+</span><span>Happy Clients</span></div>
                         <div><GiftOutlined /><span>5+</span><span>Recognitions</span></div>
                         </div>
            </div>
        )
    }
}
