import React, { Component } from 'react'
import './clients.css'
import '../../node_modules/antd/dist/antd.css';
import {Row,Col,Typography,Button} from  'antd'
import {NotificationOutlined} from '@ant-design/icons'; 
import './getus.css'
const {Title}=Typography;
class Getus extends Component
{
    render()
    {
        return(
            <div >
            <div className='my-5 d-flex flex-column align-items-center'>
                <Row className='my-3  d-flex justify-content-center' style={{width:'97%'}}>
                    <Col span={24}><Title level={3} className='text-center my-0' style={{color:'rgb(208,53,57)'}}>Who We Are</Title></Col>
                    <Col span={24}><Title level={1} className='text-center my-0 mb-4'>Let's Discuss Your Project</Title></Col>
                      <div style={{width:"150px",borderBottom:'2px solid rgb(208,53,57)'}}></div>
                </Row>
                <div className="my-3" style={{width:'97%'}}>
                      <div className='d-flex justify-content-around flex-wrap w-100 getus my-3'>
                          <div className=' py-4 mb-3'>
                              <NotificationOutlined style={{fontSize:"100px",color:"rgb(208,53,57)"}} className='mb-2'/>
                              <Title level={5} className='my-0'>I Want Website For My Business</Title>
                          </div>
                          <div className=' py-4 mb-3'>
                              <img src={process.env.PUBLIC_URL+'/img/qr.jpg'} className='mb-2'/>
                              <Title level={5} className='my-0' style={{color:"rgb(208,53,57)"}}>Scan Now To Discuss</Title>
                          </div>
                      </div>
                </div>
            </div>
            </div>
           
        )
    }
    
}
export default Getus;