import React, { Component } from 'react'
import './accordin.css'
import {Typography,Row,Col } from 'antd';
import {ArrowRightOutlined} from '@ant-design/icons';
const {Title}=Typography;
class Accor extends Component
{
    render()
    {
        return (
            <div className="bg-light accor py-5" style={{maxWidth:"100%;"}}>
            <div className="row">
              <div className="col-lg-6  d-flex flex-column   justify-content-center align-items-center">
                <div id="accordion" className="py-2 mx-0">
                    <div className="card">
                      <div className="card-header border-0  " id="headingOne" style={{backgroundImage: 'linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))'}}>
                        <div className="mb-0 d-flex justify-content-between align-items-center coll-section" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                            <Title level={5} className='my-0 text-white'>How We Work</Title>
                            <ArrowRightOutlined className='text-white arrowRight arrow-rotate'/>
                        </div>
                      </div>
                      <div id="collapseOne" className="collapse show" aria-labelledby="headingOne" data-parent="#accordion">
                        <div className="card-body">We are a team of professionals who work hard on a given project to ensure the best outcomes and meet the demands of our customers. Together with their skills and knowledge, the team builds applications that are easy to use and are appealing. We work based on data analysis that gives us the exact picture of what customers demand.</div>
                      </div>
                    </div>
                </div>
                <div id="accordion" className="py-2" >
                    <div className="card">
                      <div className="card-header border-0" id="headingTwo" style={{backgroundImage: 'linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))'}}>
                        <div className="mb-0 d-flex justify-content-between align-items-center coll-section" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                            <Title level={5} className='my-0 text-white'>What Makes Us Different</Title>
                            <ArrowRightOutlined className='text-white arrowRight arrow-rotate'/>
                        </div>
                      </div>
                      <div id="collapseTwo" className="collapse show" aria-labelledby="headingTwo" data-parent="#accordion">
                        <div className="card-body">Our passion, values, and dedication towards a given work are what make us different from others. We work together in a team to ensure that we provide the best services to our clients that will ultimately help them grow their business. </div>
                      </div>
                    </div>
                </div>
              </div>
              <div className="col-lg-6 d-flex align-items-center p-0" style={{overflow:'hidden'}}>
                  <img src={process.env.PUBLIC_URL+'/img/man.jpg'} className="img-fluid"/>
              </div>
            </div>
          </div>
        )
    }
    componentDidMount()
    {
      var arrowRight=document.getElementsByClassName("arrowRight");
      var colSection=document.getElementsByClassName("col-section");
      for(let i=0;i<colSection.length;i++)
        colSection[i].addEventListener("click",()=>{arrowRight[i].classList.toggle("arrow-rotate")})
    }
}
export default Accor;