import React, { Component } from 'react'
import './clients.css'
import '../../node_modules/antd/dist/antd.css';
import {Row,Col,Typography,Button} from  'antd'
import {RightCircleFilled} from '@ant-design/icons';
const {Title}=Typography;
class Client extends Component
{
    render()
    {
        return(
            <div >
            <div className='my-5  d-flex flex-column align-items-center'>
                <Row className='my-3  d-flex justify-content-center' style={{width:'93%'}}>
                    <Col span={24}><Title level={3} className='text-center my-0' style={{color:'rgb(208,53,57)'}}>Repla</Title></Col>
                    <Col span={24}><Title level={1} className='text-center my-0 mb-4'>Our Satisfactory Clients</Title></Col>
                      <div style={{width:"150px",borderBottom:'2px solid rgb(208,52,63)'}}></div>
                </Row>
                     <div className="bottom my-3" style={{width:'93%'}}>
                        <div className="first-box-cover row" id="animation">
                            <div className="first-box col-12 px-0 my-2">
                                <div className='mx-0 py-2'>
                                <img src={process.env.PUBLIC_URL+'/img/client one.jpg'} className='img-fluid mb-3 rounded-circle'/>
                                <Title level={4} className='my-0 mb-2'>Mike Stan, Founder Lead Juice</Title>
                                <p className='mb-5 text-center font-white-50'>"With Thrive's help, we were able to increase the Organic Traffic of our website dramatically while cutting our costs. Our website is much easier to use, has tons of more features than before, and is incredibly easy to maintain. We could not be happier with our new website! Thanks"</p>
                                <img src={process.env.PUBLIC_URL+'/img/stars.png'} className='mb-2'/>
                                <Title level={5} className='my-0 mb-2'>Lead Juice</Title>
                                </div>
                            </div>
                            <div className="first-box col-12 px-0 my-2">
                                <div className='mx-0 py-2'>
                                <img src={process.env.PUBLIC_URL+'/img/client two.jpg'} className='img-fluid mb-3 rounded-circle'/>
                                <Title level={4} className='my-0 mb-2'>Mike Stan, Founder Lead Juice</Title>
                                <p className='mb-5 text-center font-white-50'>"With Thrive's help, we were able to increase the Organic Traffic of our website dramatically while cutting our costs. Our website is much easier to use, has tons of more features than before, and is incredibly easy to maintain. We could not be happier with our new website! Thanks"</p>
                                <img src={process.env.PUBLIC_URL+'/img/stars.png'} className='mb-2'/>
                                <Title level={5} className='my-0 mb-2'>Lead Juice</Title>
                                </div>
                            </div>
                            <div className="first-box col-12 px-0 my-2">
                                <div className='mx-0 py-2'>
                                <img src={process.env.PUBLIC_URL+'/img/client2.jpg'} className='img-fluid mb-3 rounded-circle'/>
                                <Title level={4} className='my-0 mb-2'>Mike Stan, Founder Lead Juice</Title>
                                <p className='mb-5 text-center font-white-50'>"With Thrive's help, we were able to increase the Organic Traffic of our website dramatically while cutting our costs. Our website is much easier to use, has tons of more features than before, and is incredibly easy to maintain. We could not be happier with our new website! Thanks"</p>
                                <img src={process.env.PUBLIC_URL+'/img/stars.png'} className='mb-2'/>
                                <Title level={5} className='my-0 mb-2'>Lead Juice</Title>
                                </div>
                            </div>
                        </div> 
                        <div className="first-box-circle"><span ></span><span ></span><span ></span></div>
                    </div>
                
            </div>
            </div>
           
        )
    }
    componentDidMount()
    {
        var slide_translate=100;
        var current_slide_translate=0; //save current slide value
          var current_circle=1;
          var firstBox=document.getElementsByClassName("first-box");
          var first_box_cover=document.getElementsByClassName("first-box-cover")[0];
          console.log(first_box_cover.offsetWidth)
          var total_walk=0;
          function first_box(to)
          {
              current_circle=to;
              if(to==0){slide_translate=0;current_circle=0}
              if(to==1){slide_translate=100;current_circle=1}
              if(to==2){slide_translate=200;current_circle=2}
              clearInterval(interval);
              trans();
              interval=setInterval(trans,5000);
          } 
          function trans_left()
          {
             if(current_circle==1){slide_translate=200;current_circle=2;}
            else {current_circle-=2;slide_translate-=200;}
             trans();
          }    
          var interval=setInterval(trans,5000)
          function trans()
          {
             // if(window.innerWidth>992){first_box_cover.style.transform=`translate(-${0}px,0)`;slide_translate=0;current_circle=0;return}
               if(slide_translate==300){slide_translate=0;current_circle=0;total_walk=0;};first_box_cover.style.transform=`translate(-${slide_translate}%,0)`;first_box_circle(current_circle);slide_translate+=100;current_circle++;
               
                total_walk=first_box_cover.offsetWidth*(current_circle-1);
      
          }
          function trans_fixed()
          {
              current_slide_translate=slide_translate-100;  //current slide
              first_box_cover.style.transform=`translate(-${current_slide_translate}%,0)`;
          }
      function first_box_circle(j)
      {
        var current_circle=document.getElementsByClassName("first-box-circle")[0].getElementsByTagName("span");
        for(let i=0;i<current_circle.length;i++)
            current_circle[i].style.backgroundColor='rgb(208,52,63)';
        current_circle[j].style.backgroundColor=' rgb(231,31,31)';
      }
      var slider=document.getElementsByClassName("first-box-cover")[0];
              var isdown=false;
              var startX;
              var top_move=false;
              var top_walk;
              slider.addEventListener("mousedown",(e)=>{
                isdown=true;
                startX=e.pageX; 
                clearInterval(interval);
              })
              slider.addEventListener("touchstart",(e)=>{
                isdown=true;
                startX=e.touches[0].clientX;
                clearInterval(interval);
              })
              slider.addEventListener("mouseleave",()=>{
                  isdown=false;
                  if(top_move) interval=setInterval(trans,5000);
                  top_move=false;
              })
              slider.addEventListener("touchcancel",()=>{
                  isdown=false;
                  if(top_move) interval=setInterval(trans,5000);
                  top_move=false;
              })
              slider.addEventListener("mouseup",()=>{
                  isdown=false;
                  first_box_cover.setAttribute('id','animation');
                  console.log('top_Walk:',top_walk)
                  if(top_move && top_walk<0 && top_walk<-200)
                  {trans();console.log("yes right")}
                  else trans_fixed();
                  if(top_move && top_walk>0 && top_walk>200)
                  {trans_left();console.log("yes left")}
                  else trans_fixed();
                  top_move=false;
                 interval=setInterval(trans,5000);
              })
              slider.addEventListener("touchend",()=>{
                  isdown=false;
                  first_box_cover.setAttribute('id','animation');
                  console.log('top_Walk:',top_walk)
                  if(top_move && top_walk<0 && top_walk<-200)
                  {trans();console.log("yes right")}
                  else trans_fixed();
                  if(top_move && top_walk>0 && top_walk>200)
                  {trans_left();console.log("yes left")}
                  else trans_fixed();
                  top_move=false;
                 interval=setInterval(trans,5000);
              })
              slider.addEventListener("mousemove",(e)=>{
                  if(!isdown) return
                  e.preventDefault();
                  top_move=true;
                  top_walk=e.pageX-startX;
                  first_box_cover.setAttribute('id','')
                 if(slide_translate==100){total_walk=0;}
                  first_box_cover.style.transform=`translate(${-total_walk+top_walk}px,0)`;
              })
              slider.addEventListener("touchmove",(e)=>{
                  if(!isdown) return
                //  e.preventDefault();
                  top_move=true;
                  top_walk=e.touches[0].clientX-startX;
                  first_box_cover.setAttribute('id','')
                 if(slide_translate==100){total_walk=0;}
                  first_box_cover.style.transform=`translate(${-total_walk+top_walk}px,0)`;
              })
    }
}
export default Client;