import React from 'react'
import {Typography,Row,Col } from 'antd';
import './serve.css'
import TitleTop from './title';
import {EnvironmentOutlined,PhoneOutlined,MailOutlined} from '@ant-design/icons';
import ContactUsForm from './contactus form'
const {Title}=Typography;
const ContactUs=()=>
{
        return    (
            <>
             <TitleTop t1='Contact Us' t2='We Can Help You Succeed'/>
            <div className="my-5 d-flex flex-column align-items-center">
             <Row className='mb-3  d-flex justify-content-center' style={{width:'97%'}}>
                       <Col span={24}><Title level={3} className='my-0 text-center' style={{color:'rgb(208,53,57)'}}>OUR CONTACTS</Title></Col>
                       <Col span={24}><Title level={1} className='my-0 mb-2 text-center'>We’re Here to Help You</Title></Col>
                       <Col span={24}><Title level={5} className='my-0 mb-4 text-center'>Got a project in mind? We’d love to hear about it.</Title></Col>
                        <div style={{width:"150px",borderBottom:'2px solid rgb(208,53,57)'}}></div>
              </Row>
             <div className="box-cover my-3 justify-content-around" style={{width:'97%'}}>
               <div className="box border my-2 py-4"><EnvironmentOutlined className='mb-2 main-clr'/><Title level={4} className='mb-0 main-clr'>Visit Us Daily:</Title><Title level={5} className='text-center mt-1'>B-Block, Satellite Town Rawalpindi, Punjab, Pakistan</Title></div>
               <div className="box border my-2 py-4"><PhoneOutlined className='mb-2 main-clr'/><Title level={4} className='mb-0 main-clr'>Phone Us 24/7:</Title><Title level={5} className='text-center mt-1'>+92 344-5592382</Title></div>
               <div className="box border my-2 py-4"><MailOutlined className='mb-2 main-clr'/><Title level={4} className='mb-0 main-clr'>Mail Us 24/7:</Title><Title level={5} className='text-center mt-1'>officals@repla.com</Title></div>
             </div>
              <iframe style={{width:"100%",height:"500px",border:'none'}} src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d13287.87505581608!2d73.0652489!3d33.6320519!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x8a237df750ecb3fc!2sRepla%20Security%20and%20Technology!5e0!3m2!1sen!2s!4v1618399989489!5m2!1sen!2s"  loading="lazy"></iframe>
             <ContactUsForm/>
          </div>
          </>
        )
}
export default ContactUs;
