import React, { Component } from 'react'
import {Typography,Row,Col } from 'antd';
import {HeartOutlined,ShoppingCartOutlined,DesktopOutlined,LineChartOutlined} from '@ant-design/icons';
import './serve.css'
const {Title}=Typography;
class Serve extends Component
{
    render()
    {
        return    (
            <div className="my-5 d-flex flex-column align-items-center">
                <Row className='mb-3  d-flex' style={{width:'98%'}}>
                          <Col span={24}><Title level={3} className='my-0' style={{color:'rgb(208,53,57)'}}>Industries we serve</Title></Col>
                          <Col span={24}><Title level={1} className='my-0 mb-4'>We Use the industry standard pratices</Title></Col>
                            <div style={{width:"150px",borderBottom:'2px solid rgb(208,53,57)'}}></div>
                  </Row>
                <div className="box-cover my-3" style={{width:'98%'}}>
                  <div className="box border my-2 py-4"><HeartOutlined className='mb-2 main-clr'/><Title level={4}>Health Care</Title></div>
                  <div className="box border my-2 py-4"><ShoppingCartOutlined className='mb-2 main-clr'/><Title level={4}>Retail and e-commerce</Title></div>
                  <div className="box border my-2 py-4"><DesktopOutlined className='mb-2 main-clr'/><Title level={4}>Education</Title></div>
                  <div className="box border my-2 py-4"><LineChartOutlined className='mb-2 main-clr'/><Title level={4}>Marketing</Title></div>
                </div>
             <div className="slider my-3 shadow border" style={{width:'98%'}}>
               <div className="slider-inner">
                 <div className="row slider-box py-3">
                   <div className="col-md-6"><img src={process.env.PUBLIC_URL+'/img/serve/i2.png'} className="img-fluid"/></div>
                   <div className="col-md-6 " >
                     <Title level={3} style={{color:'rgb(208,53,57)'}}>Healthcare</Title>
                     <p className='text-justify'>Our artificial intelligence services are built to provide quicker and better healthcare solutions. People can now consult with the doctors through smartphones and get healthcare solutions without stepping out of the house. Data can be easily stored in digital form.</p>
                     <ul>
                       <li>Digital consultation</li>
                       <li>Easy data maintenance</li>
                       <li>Real-time communication</li>
                     </ul>
                   </div>
                 </div>
                 {/*  */}
                 <div className="row slider-box py-3">
                   <div className="col-md-6"><img src={process.env.PUBLIC_URL+'/img/serve/i1.jpg'} className="img-fluid"/></div>
                   <div className="col-md-6">
                     <Title level={3} style={{color:'rgb(208,53,57)'}}>Retail and e-commerce</Title>
                     <p className='text-justify'>Artificial intelligence is beneficial for the retail and e-commerce industries. They are using it to analyze and predict customer’s intentions in advance to produce products that their customers want. From product recommendation, buying the product, to making online payments, AI can make all these procedures quick and safe.</p>
                     <ul>
                       <li>Understand customer’s needs</li>
                       <li>Improved recommendations for customer</li>
                       <li>Chatbots and personal assistants for 24/7 customer service</li>
                     </ul>
                   </div>
                 </div>
                {/*  */}
                {/*  */}
                  <div className="row slider-box py-3">
                   <div className="col-md-6"><img src={process.env.PUBLIC_URL+'/img/serve/i3.png'} className="img-fluid"/></div>
                   <div className="col-md-6">
                     <Title level={3} style={{color:'rgb(208,53,57)'}}>Education</Title>
                     <p className='text-justify'> AI has brought many opportunities in the education industry as well. It has improved the learning process and has made it possible for education to reach out to the masses. Clearing doubts, submitting projects, and interacting with the teachers can all be done with AI. It is continuously making the teaching process more efficient and quickly available to more. Through this, your brand can access:</p>
                     <ul>
                       <li>Education at any time</li>
                       <li>Virtual mentors</li>
                       <li>Better engagement</li>
                     </ul>
                   </div>
                 </div>
                 {/*  */}
                {/*  */}
                  <div className="row slider-box py-3">
                   <div className="col-md-6"><img src={process.env.PUBLIC_URL+'/img/serve/i4.png'} className="img-fluid"/></div>
                   <div className="col-md-6">
                   <Title level={3} style={{color:'rgb(208,53,57)'}}>Marketing</Title>
                     <p>AI is transforming marketing and has made many marketing tasks speed up. It has further improved customer experience and has driven more conversions. With the help of AI, marketers gain a better and more in-depth knowledge of their customers, enabling them to deliver the right message and products.</p>
                     <ul>
                       <li>360-degree customer view</li>
                       <li>Multichannel reach</li>
                       <li>Data segmentation</li>
                     </ul>
                   </div>
                 </div>
                  {/*  */}
               </div>
             </div>
          </div>
        )
    }
    componentDidMount()
    {
                var slider=document.querySelector(".slider");
                var slider_inner=slider.firstElementChild;
                var slider_box=document.getElementsByClassName("slider-box");
                
                slider_inner.style.width=`${slider_inner.children.length*100}%`
                var our_services=document.getElementsByClassName("box");
                for(let i=0;i<our_services.length;i++)
                {
                our_services[i].addEventListener("click",()=>{show_slide(i)})
                }
                const show_slide=(i)=>{
                slider_inner.style.transform=`translateX(${-slider_box[0].offsetWidth*i}px)`;
                console.log(slider_box[0].offsetWidth)
                }
                function resize()
                {
                for(let i=0;i<slider_box.length;i++)
                    slider_box[i].style.width=`${slider.offsetWidth}px`
                }
                resize();
    }
}
export default Serve;
