import React, { Component } from 'react'
import '../../node_modules/antd/dist/antd.css';
import { Timeline,Row,Col,Typography } from 'antd';
import { AimOutlined,EyeOutlined } from '@ant-design/icons';
import './cards.css'
const {Title}=Typography;

class Cards extends Component
{
    render()
    {
        return (
                     <div >
                     <div className='my-5 d-flex flex-column align-items-center'>
                         <Row className='my-0 mb-3  d-flex justify-content-center' style={{width:'97%'}}>
                             <Col span={24}><Title level={3} className='text-center my-0 ' style={{color:'rgb(208,53,57)'}}>Repla</Title></Col>
                             <Col span={24}><Title level={1} className='text-center my-0 mb-4'>Our Core Belief</Title></Col>
                               <div style={{width:"150px",borderBottom:'2px solid rgb(208,52,63)'}}></div>
                         </Row>
                         <div style={{width:'97%'}} className='my-3 d-flex justify-content-center'>
                         <div className="row w-100">
                                    <div className="col-md-6 pl-0 my-2">
                                        <div className="scene scene_card">
                                            <div className="cards">
                                                <p className="card__face card__face_front ">
                                                <AimOutlined style={{fontSize:"100px"}} className='mb-3'/>
                                                    <span>Our mission is to build such applications that are easy for everyone to understand and can be helpful for them in their daily lives. We also aim at rendering a class quality to our clients so that they can shine and build their names.</span>
                                                </p>
                                                <Title level={1}  className='text-center card__face card__face_back text-white'>Mission</Title>
                                            </div>
                                            </div>
                                    </div>
                                    <div className="col-md-6 pr-0 my-2">
                                        <div className="scene scene_card">
                                            <div className="cards">
                                                <Title className='card__face card__face_front text-white' level={1}>Our Vision</Title>
                                                <p className="card__face card__face_back">
                                                <EyeOutlined style={{fontSize:"100px"}} className='mb-3'/>
                                                    <span>At present and also in the future, we want to be of help for all kinds of businesses to help design and build applications for their customers based on their needs and goals. 1Touch believes that together you and we can create a digital world that will be helpful for everyone in the community.</span></p>
                                            </div>
                                            </div>
                                    </div>                
                                   </div>
                         </div>
                     </div>
                 </div>
        )
    }
    componentDidMount()
    {
        var card = document.querySelectorAll('.cards');
        for(let i=0;i<card.length;i++)
        {
          card[i].addEventListener( 'mouseenter', ()=>{ card[i].classList.toggle('is_flipped')});
          card[i].addEventListener( 'mouseleave', ()=>{ card[i].classList.toggle('is_flipped')});
        }
    }
}
export default Cards;