import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import  './navbar.css'
import '../../node_modules/antd/dist/antd.css';
import {ArrowRightOutlined,DownOutlined} from '@ant-design/icons';
import {Button} from  'antd'
class Navbar extends Component
{
    render()
    {
        return  (
            <>
           <div>
           <div className="repla-navbar-small">
                    <div className="repla-navbar-inner">
                        <div className='repla-navbar-left'><img src={process.env.PUBLIC_URL+'/img/logo.png'}/></div>
                        <div className="repla-navbar-right">
                        <div className="sp-cover">
                            <span className="sp sp-1"></span>
                            <span className="sp sp-2"></span>
                            <span className="sp sp-3"></span>
                        </div>
                        </div>
                    </div>
             </div>
            <div className='repla-navbar'>
                <div className="repla-navbar-inner">
                    <div className='repla-navbar-left'><img src={process.env.PUBLIC_URL+'/img/logo.png'}/></div>
                    <ul className="repla-navbar-middle mb-0">
                    <li ><NavLink to="/" className="arrow"><ArrowRightOutlined />Home</NavLink></li>
                    <li className="items"><NavLink to='' className="arrow"><ArrowRightOutlined />Company<DownOutlined className='arrowDown'/></NavLink><ul><li className="li"><NavLink to="/AboutUs">About Us</NavLink><span></span></li><li className="li"><NavLink to='/Protfolio'>Porfolio</NavLink><span></span></li></ul></li>
                    <li className="items"><NavLink to="" className="arrow"><ArrowRightOutlined />Services<DownOutlined className='arrowDown'/></NavLink><ul><li className="li"><NavLink to='/WebDev'>Web Development</NavLink><span></span></li><li className="li"><NavLink to='/AppDev'>App Development</NavLink><span></span></li><li className="li"><NavLink to='/CRM'>CRM/ERP Development</NavLink><span></span></li><li className="li"><NavLink to='/DigitalMarketing'>Digital Marketing</NavLink><span></span></li><li className="li"><NavLink to='ContentCreation'>Content Creation</NavLink><span></span></li><li className="li"><NavLink to='DesignServices'>Design Services</NavLink><span></span></li></ul></li>
                    <li ><NavLink to="/WebDev" className="arrow"><ArrowRightOutlined />Our Team</NavLink></li>
                    <li ><NavLink to="/ContactUs" className="arrow"><ArrowRightOutlined />Contact</NavLink></li>
                    </ul>
                    <div className="repla-navbar-right">
                    <Button type='primary' size='large' shape='round' style={{backgroundColor:'rgb(208,53,57)',borderColor:"rgb(208,53,57)"}} className='my-3'>Let's Get started</Button>
                    </div>
                </div>
            </div>
           </div>
            </>
        )
    }
    componentDidMount()
    {
        
        var items=document.getElementsByClassName("items");
        var arrow=document.getElementsByClassName("arrow");
        var arrowDown=document.getElementsByClassName("arrowDown");
        console.log(arrowDown)
        var navbar=document.getElementsByClassName("repla-navbar")[0];
        var navbarSmall=document.getElementsByClassName("repla-navbar-small")[0];
        console.log(navbar.offsetHeight)
        console.log("items:",items[0].offsetHeight);
        for(let i=0;i<items.length;i++)
        {
            
            items[i].getElementsByTagName("a")[0].addEventListener("mouseenter",()=>{items[i].getElementsByTagName("ul")[0].style.display='flex';arrowDown[i].style.transform=`rotate(180deg)`})
            items[i].addEventListener("mouseleave",()=>{items[i].getElementsByTagName("ul")[0].style.display='none';arrowDown[i].style.transform=`rotate(0deg)`});
        }
        for(let i=0;i<arrow.length;i++)
        {
           arrow[i].addEventListener("mouseenter",()=>{arrow[i].children[0].style.transform='translateX(0px)';arrow[i].children[0].style.visibility='visible';})
           arrow[i].parentElement.addEventListener("mouseleave",()=>{arrow[i].children[0].style.transform='translateX(-15px)';arrow[i].children[0].style.visibility='hidden';})
        }
        var box=document.getElementsByClassName("sp-cover")[0];
        console.log(box)
        box.addEventListener("click",()=>{
            navbar.classList.toggle("repla-navbar-trans")
            console.log(navbar)
        })
        const responsive=()=>
        {
            console.log("yes")
            if(window.innerWidth<1000)
           {navbar.style.height=`${window.innerHeight-105}px`;navbar.style.top='105px'
        }
            else
            {navbar.style.height=`${105}px`;navbar.setAttribute("class","repla-navbar");navbar.style.top='0px'}
        }
        var li=document.getElementsByClassName("li");
        for(let i=0;i<li.length;i++)
        {
            li[i].addEventListener("mouseenter",()=>{
                li[i].lastElementChild.style.width='100%';
            })
            li[i].addEventListener("mouseleave",()=>{
                li[i].lastElementChild.style.width='30%';
            })
        }
        responsive();
        window.addEventListener("resize",()=>{responsive(); scrol();})
        function scrol()
                {
                    if(window.innerWidth>1000)
                    {
                        if(window.pageYOffset<15)
                        {navbar.style.background='transparent';navbar.setAttribute("class","repla-navbar")}
                         else
                         {navbar.style.backgroundImage='linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))';navbar.setAttribute("class","repla-navbar nav-shadow")}
                    }
                    else
                    {
                        navbar.style.backgroundImage='linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))'
                        navbar.setAttribute("class","repla-navbar");
                        if(window.pageYOffset<11)
                        {navbarSmall.style.background='transparent';navbarSmall.setAttribute("class","repla-navbar-small")}
                         else
                         {navbarSmall.style.backgroundImage='linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))';navbarSmall.setAttribute("class","repla-navbar-small nav-shadow")}
                    }
                   
                }  
                scrol();
                window.onscroll = function() {scrol()}
    }
}
export default Navbar;
